@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,400i,700);
@charset "UTF-8";

/* Reset CSS */
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: top;
  background: transparent;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}
* ul, * li {
  list-style: none;
  margin: 0;
  padding: 0;
}

input, button {
  border: none;
  outline: none;
}
button {
  cursor: pointer;
}

html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  font-size: 16px;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.headerContainer {
  position: relative;
  height: 600px;
  background: #fffdfd;
  overflow: hidden;
  transition: 0.2s;
}
.headerContainer .background {
  position: absolute;
  width: 150vw;
  height: 100%;
  right: 0;
  background: linear-gradient(15deg, #ffd8d8, #cbe6ff);
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
  transition: 0.2s;
  z-index: 0;
}
.headerContainer .userInfo {
  display: flex;
  padding-top: 30px;
}
.headerContainer .photo {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  background: url(/my-react-resume/static/media/me.4bd586e0.jpg) center center no-repeat;
  background-size: cover;
}
.headerContainer .titleArea {
  margin-left: 40px;
}
.headerContainer .title {
  margin-top: 40px;
  color: #443163;
  letter-spacing: 1px;
  font-size: 200%;
  font-family: 微軟正黑體;
}
.headerContainer .subtitle {
  margin: 5px 0 40px 0;
  color: #554b63;
  font-size: 115%;
  font-family: 微軟正黑體;
  font-weight: 500;
}
.headerContainer .contact {
  position: relative;
  margin-top: 10px;
  padding-left: 70px;
  color: #554b63;
  font-size: 115%;
  font-family: 微軟正黑體;
  font-weight: 900;
}
.headerContainer .contact.email::before {
  content: 'E-mail： ';
  position: absolute;
  left: 0;
  top: 0;
}
.headerContainer .contact.tel::before {
  content: 'Phone： ';
  position: absolute;
  left: 0;
  top: 0;
}
.headerContainer .links {
  display: flex;
  margin-top: 15px;
}
.headerContainer .links li {
  position: relative;
  display: flex;
  margin-right: 15px;
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.2s;
}
.headerContainer .links li:hover {
  background: #554b63;
}
.headerContainer .links li:hover::before {
  color: #ffffff;
}
.headerContainer .links li::before {
  font-family: FontAwesome;
  font-size: 200%;
  color: #554b63;
  transition: 0.2s;
}
.headerContainer .links .github::before {
  content: '\f09b';
  font-size: 230%;
}
.headerContainer .links .codepen::before {
  content: '\f1cb';
}
.headerContainer .links .facebook::before {
  content: '\f082';
}
.headerContainer .links .instagram::before {
  content: '\f16d';
}
.headerContainer .links .youtube::before {
  content: '\f16a';
}
.headerContainer .links a {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

/* Dark Theme */
.headerContainer.dark {
  background: #5f5d5d;
}
.headerContainer.dark .background {
  background: linear-gradient(15deg, #332b2b, #424b53);
}

.contentContainer {
  position: relative;
  height: 200px;
  background: #fffdfd;
  transition: 0.2s;
}

/* Dark Theme */
.contentContainer.dark {
  background: #5f5d5d;
}
.layoutContainer .container {
  position: relative;
  width: calc(100vw - (240px + 10px));
  margin-left: calc(240px + 10px);
  padding: 0 0 0 100px;
  transition: 0.2s;
}
.sidebarContainer {
  position: fixed;
  width: 240px;
  height: calc(100vh - 20px);
  top: 10px;
  left: 10px;
  padding: 15px;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.7);
  box-shadow: 0 2px 5px rgba(59, 52, 52, 0.5);
  transition: 0.2s;
}
.sidebarContainer .header {
  /* font-family: 微軟正黑體; */
  padding-bottom: 15px;
  font-size: 180%;
  font-weight: 500;
  color: #555;
  text-align: left;
  border-bottom: 2px solid #b8b8b8;
  transition: 0.2s;
}
.sidebarContainer .navList {
  height: calc(100% - 100px);
  margin-top: 10px;
  overflow-y: auto;
  transition: 0.2s;
}
.sidebarContainer .navItem {
  /* background: #e0deec; */
  position: relative;
  padding: 10px 10px 10px 45px;
  margin-bottom: 10px;
  border-radius: 10px;
  font-size: 120%;
  cursor: pointer;
  color: #443163;
  overflow: hidden;
  transition: 0.1s;
}
.sidebarContainer .navItem:hover,
.sidebarContainer .navItem.active {
  background: #e0deec;
}
.sidebarContainer .navItem:hover::before,
.sidebarContainer .navItem.active::before {
  opacity: 1;
}
.sidebarContainer .navItem:hover .navItemDetail {
  margin-top: 5px;
  max-height: 50px;
  opacity: 1;
  /* transition: margin-top 0. */
}
.sidebarContainer .navItem::before {
  position: absolute;
  height: 25px;
  width: 25px;
  top: 9px;
  left: 10px;
  font-family: FontAwesome;
  text-align: center;
  line-height: 25px;
  font-size: 130%;
  opacity: 0.5;
  transition: 0.1s;
}
.sidebarContainer .navItem.basicInfo::before {
  content: '\f007';
}
.sidebarContainer .navItem.experience::before {
  content: '\f19d';
  font-size: 115%;
  padding-left: 1px;
}
.sidebarContainer .navItem.skills::before {
  content: '\f0ad';
  font-size: 125%;
}
.sidebarContainer .navItem.about::before {
  content: '\f1ae';
}
.sidebarContainer .navItem.autobiography::before {
  content: '\f200';
  font-size: 115%;
  padding-left: 1px;
}
.sidebarContainer .navItem.works::before {
  content: '\f109';
  font-size: 125%;
  padding-left: 1px;
}
.sidebarContainer .navItem.resume::before {
  content: '\f2c2';
  font-size: 115%;
  padding-left: 1px;
}
.sidebarContainer .navItemDetail {
  font-size: 80%;
  margin-top: 0px;
  max-height: 0px;
  word-break: keep-all;
  overflow: hidden;
  opacity: 0;
  transition: opacity 0.2s 0.3s, margin-top 0.2s 0.3s, max-height 0.2s 0.3s;
}

/* Dark Theme */
.sidebarContainer.dark {
  background: rgba(27, 25, 24, 0.9);
  box-shadow: 0;
}
.sidebarContainer.dark .header {
  color: #a5a5a5;
  text-align: left;
  border-bottom: 2px solid #3a3a3a;
}
.sidebarContainer.dark .navItem {
  color: #c8c4d1;
}
.sidebarContainer.dark .navItem:hover,
.sidebarContainer.dark .navItem.active {
  background: #3b3938;
  color: #FFF;
}

